import React from 'react';
import { Navbar, Container, Form, Button, ButtonGroup, Nav } from 'react-bootstrap';

interface HeaderProps {
  swing: number;
  setSwing: (value: number) => void;
  swingMode: '16th' | '8th';
  setSwingMode: (mode: '16th' | '8th') => void;
  isPlaying: boolean;
  onPlayToggle: () => void;
}

const Header: React.FC<HeaderProps> = ({ swing, setSwing, swingMode, setSwingMode, isPlaying, onPlayToggle }) => {
  return (
    <Navbar bg="dark" variant="dark" expand="lg" style={{ backgroundColor: 'var(--surface-color)' }}>
      <Container fluid>
        <Navbar.Brand href="#">Groovebox</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="w-100 d-flex justify-content-between">
            <div className="d-flex align-items-center">
              <Button variant="primary" className="me-2" onClick={onPlayToggle}>{isPlaying ? '■' : '▶'}</Button>
              <Form.Control
                type="number"
                defaultValue={120}
                style={{ width: '80px', textAlign: 'center' }}
                className="me-2"
              />
              <Navbar.Text>BPM</Navbar.Text>
            </div>
            <div className="d-flex align-items-center">
              <Form.Label className="me-2 mb-0 text-white">Swing</Form.Label>
              <Form.Range
                min="50"
                max="75"
                value={swing}
                onChange={(e) => setSwing(parseInt(e.target.value))}
                style={{ width: '100px' }}
                className="me-2"
              />
              <ButtonGroup size="sm">
                <Button variant={swingMode === '16th' ? 'primary' : 'outline-primary'} onClick={() => setSwingMode('16th')}>16</Button>
                <Button variant={swingMode === '8th' ? 'primary' : 'outline-primary'} onClick={() => setSwingMode('8th')}>8</Button>
              </ButtonGroup>
            </div>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default Header;