import * as Tone from 'tone';

// Define the structure for a single step's data, matching the UI state
interface StepState {
  note: number;
  pLockValues: { [key: string]: number };
}

// Define the structure for the entire sequencer state from the UI
interface SequencerState {
  drumGrid: boolean[][];
  bassSteps: StepState[];
  padSteps: StepState[];
  leadSteps: StepState[];
  swing: number;
  swingMode: '16th' | '8th';
  drumVolumes: number[]; // Added drum volumes
  bassVolume: number;    // Added bass volume
  padVolume: number;     // Added pad volume
  leadVolume: number;    // Added lead volume
}

// Helper to map UI slider values (0-100) to Tone.js parameters
const mapValue = (value: number, minOut: number, maxOut: number) => {
  return (value / 100) * (maxOut - minOut) + minOut;
};

/**
 * The AudioEngine class is responsible for all audio-related operations.
 * It uses Tone.js to schedule and play sounds based on the state provided by the UI.
 */
export class AudioEngine {
  private synths: Tone.Synth[];
  private drumSynths: (Tone.MembraneSynth | Tone.MetalSynth)[];
  private filters: Tone.Filter[];
  private gains: Tone.Gain[]; // Added Gain nodes for melodic parts
  private drumGains: Tone.Gain[]; // Added Gain nodes for drum parts
  private onStepChange: (step: number) => void = () => {};
  private currentSequencerState: SequencerState | null = null;

  constructor() {
    // Initialize Gain nodes for melodic parts
    this.gains = [
      new Tone.Gain(1).toDestination(), // Bass Gain
      new Tone.Gain(1).toDestination(), // Pad Gain
      new Tone.Gain(1).toDestination(), // Lead Gain
    ];

    // Initialize filters and connect them to gains
    this.filters = [
      new Tone.Filter(2000, 'lowpass').connect(this.gains[0]), // Bass Filter -> Bass Gain
      new Tone.Filter(2000, 'lowpass').connect(this.gains[1]), // Pad Filter -> Pad Gain
      new Tone.Filter(2000, 'lowpass').connect(this.gains[2]), // Lead Filter -> Lead Gain
    ];

    // 1. Initialize instruments for each part
    this.synths = [
      new Tone.Synth({
        oscillator: { type: 'triangle' },
        envelope: { attack: 0.05, decay: 0.1, sustain: 0.3, release: 0.5 },
      }).connect(this.filters[0]), // Bass
      new Tone.Synth({
        oscillator: { type: 'sine' },
        envelope: { attack: 0.5, decay: 0.5, sustain: 0.5, release: 1 },
      }).connect(this.filters[1]), // Pad (Changed from PolySynth to Synth)
      new Tone.Synth({
        oscillator: { type: 'sawtooth' },
        envelope: { attack: 0.01, decay: 0.2, sustain: 0.5, release: 0.5 },
      }).connect(this.filters[2]), // Lead
    ];

    // Initialize Gain nodes for drum parts
    this.drumGains = [
      new Tone.Gain(1).toDestination(), // BD Gain
      new Tone.Gain(1).toDestination(), // SD Gain
      new Tone.Gain(1).toDestination(), // CH Gain
      new Tone.Gain(1).toDestination(), // OH Gain
    ];

    this.drumSynths = [
        new Tone.MembraneSynth({ octaves: 4, pitchDecay: 0.01 }).connect(this.drumGains[0]), // BD -> BD Gain
        new Tone.MembraneSynth({ octaves: 8, pitchDecay: 0.1 }).connect(this.drumGains[1]), // SD -> SD Gain
        new Tone.MetalSynth({ envelope: { attack: 0.001, decay: 0.1, release: 0.01 }, harmonicity: 5.1, modulationIndex: 32, resonance: 4000, octaves: 1.5 }).connect(this.drumGains[2]), // CH -> CH Gain
        new Tone.MetalSynth({ envelope: { attack: 0.001, decay: 0.4, release: 0.01 }, harmonicity: 5.1, modulationIndex: 32, resonance: 4000, octaves: 1.5 }).connect(this.drumGains[3]), // OH -> OH Gain
    ];

    // 2. Create a sequence that triggers on every 16th note
    const stepIndices = Array.from(Array(16).keys()); // [0, 1, 2, ..., 15]
    new Tone.Sequence((time, step) => {
      // Notify the UI about the current step
      Tone.Draw.schedule(() => {
        this.onStepChange(step);
      }, time);

      if (!this.currentSequencerState) return;

      const { drumGrid, bassSteps, padSteps, leadSteps } = this.currentSequencerState;

      // --- Drum Part --- (BD, SD, CH, OH)
      drumGrid.forEach((row, drumIndex) => {
        if (row[step]) {
          // For MetalSynths, trigger with a frequency
          if (drumIndex === 2 || drumIndex === 3) { // CH or OH
            (this.drumSynths[drumIndex] as Tone.MetalSynth).triggerAttackRelease('C4', '16n', time);
          } else {
            (this.drumSynths[drumIndex] as Tone.MembraneSynth).triggerAttackRelease('C2', '16n', time);
          }
        }
      });

      // --- Melodic Parts (Bass, Pad, Lead) ---
      const melodicParts = [
        { steps: bassSteps, synth: this.synths[0], filter: this.filters[0], baseMidi: 36, rows: 8, defaultDuration: '8n' }, // Bass (C2)
        { steps: padSteps, synth: this.synths[1], filter: this.filters[1], baseMidi: 48, rows: 8, defaultDuration: '4n' }, // Pad (C3)
        { steps: leadSteps, synth: this.synths[2], filter: this.filters[2], baseMidi: 60, rows: 12, defaultDuration: '16n' }, // Lead (C4)
      ];

      melodicParts.forEach(part => {
        const currentStepData = part.steps[step];
        if (currentStepData.note !== -1) {
          const midiNote = part.baseMidi + (part.rows - 1 - currentStepData.note);
          const note = Tone.Midi(midiNote).toNote();

          // Apply P-Locks
          const pLocks = currentStepData.pLockValues;
          let duration: Tone.Unit.Time = part.defaultDuration; // Explicitly type duration

          // Filter Frequency
          if (pLocks.Filter !== undefined) {
            part.filter.frequency.value = mapValue(pLocks.Filter, 50, 10000);
          } else {
            part.filter.frequency.value = 2000; // Default filter freq
          }

          // Gate (Note Duration)
          if (pLocks.Gate !== undefined) {
            // Map 0-100 to 0.01-0.9 of the default duration
            duration = Tone.Time(part.defaultDuration).toSeconds() * mapValue(pLocks.Gate, 0.01, 0.9);
          }

          // ADSR Envelope
          // Apply envelope changes directly to the synth's envelope object
          if (pLocks.Attack !== undefined) part.synth.envelope.attack = mapValue(pLocks.Attack, 0.01, 2);
          if (pLocks.Decay !== undefined) part.synth.envelope.decay = mapValue(pLocks.Decay, 0.01, 2);
          if (pLocks.Sustain !== undefined) part.synth.envelope.sustain = mapValue(pLocks.Sustain, 0, 1);
          if (pLocks.Release !== undefined) part.synth.envelope.release = mapValue(pLocks.Release, 0.01, 2);

          part.synth.triggerAttackRelease(note, duration, time);
        }
      });

    }, stepIndices, '16n').start(0);

    console.log('AudioEngine initialized');
  }

  /**
   * Registers a callback function to be invoked on each step of the sequence.
   * @param callback - The function to call with the current step index.
   */
  public setOnStepChange(callback: (step: number) => void) {
    this.onStepChange = callback;
  }

  /**
   * Updates the internal sequencer state from the UI.
   * @param newState - The latest state of all sequencers.
   */
  public updateSequencerState(newState: SequencerState) {
    this.currentSequencerState = newState;
    // Update Tone.Transport swing settings
    Tone.Transport.swing = (newState.swing - 50) / 100; // Map 50-75 to 0-0.25
    Tone.Transport.swingSubdivision = newState.swingMode === '16th' ? '16n' : '8n';

    // Apply global volume changes
    // Drum volumes
    newState.drumVolumes.forEach((vol, index) => {
      this.drumGains[index].gain.value = Tone.dbToGain(mapValue(vol, -40, 0)); // Map 0-100 to -40dB to 0dB
    });
    // Melodic part volumes
    this.gains[0].gain.value = Tone.dbToGain(mapValue(newState.bassVolume, -40, 0));
    this.gains[1].gain.value = Tone.dbToGain(mapValue(newState.padVolume, -40, 0));
    this.gains[2].gain.value = Tone.dbToGain(mapValue(newState.leadVolume, -40, 0));
  }

  /**
   * Starts the audio context. This must be called after a user interaction.
   */
  public async start() {
    await Tone.start();
    console.log('Audio context started');
  }

  /**
   * Starts or stops the master transport.
   * @param isPlaying - Whether the transport should be playing.
   */
  public togglePlayback(isPlaying: boolean) {
    if (isPlaying) {
      Tone.Transport.start();
    } else {
      Tone.Transport.stop();
      // When stopping, clear the playhead
      this.onStepChange(-1); // Use -1 or null to indicate stopped
    }
  }
}