import React from 'react';
import { Form } from 'react-bootstrap';
import './Sequencer.css';

interface DrumMachineProps {
  swing: number;
  swingMode: '16th' | '8th';
  activeStep: number;
  grid: boolean[][];
  setGrid: (newGrid: boolean[][]) => void;
  drumVolumes: number[];
  setDrumVolumes: React.Dispatch<React.SetStateAction<number[]>>;
}

const drumLabels = ['BD', 'SD', 'CH', 'OH'];

const DrumMachine: React.FC<DrumMachineProps> = ({ swing, swingMode, activeStep, grid, setGrid, drumVolumes, setDrumVolumes }) => {
  const rows = 4;
  const cols = 16;

  const toggleCell = (row: number, col: number) => {
    const newGrid = grid.map((r, rowIndex) =>
      rowIndex === row
        ? r.map((c, colIndex) => (colIndex === col ? !c : c))
        : r
    );
    setGrid(newGrid);
  };

  const handleDrumVolumeChange = (index: number, value: number) => {
    const newVolumes = [...drumVolumes];
    newVolumes[index] = value;
    setDrumVolumes(newVolumes);
  };

  const getCellStyle = (colIndex: number) => {
    const swingRatio = (swing - 50) / 50;
    const baseWidth = 100 / cols;
    let width = baseWidth;

    let shouldSwing = swingMode === '16th' ? colIndex % 2 !== 0 : (colIndex % 4 === 2 || colIndex % 4 === 3);

    if (shouldSwing) {
      width -= baseWidth * swingRatio;
    } else {
      width += baseWidth * swingRatio;
    }

    return { flex: `0 0 ${width}%` };
  };

  const getCellClassName = (colIndex: number) => {
    let className = 'sequencer-cell';
    if (colIndex === activeStep) className += ' playhead';
    return className;
  }

  return (
    <div>
      <div className="d-flex">
          <div className="drum-labels">
              {drumLabels.map((label, index) => (
                  <div key={index} className="drum-label">{label}</div>
              ))}
          </div>
          <div className="sequencer-grid" style={{width: '100%'}}>
          {grid.map((row, rowIndex) => (
              <div key={rowIndex} className="sequencer-row">
              {row.map((cell, colIndex) => (
                  <div
                  key={colIndex}
                  className={getCellClassName(colIndex) + (grid[rowIndex][colIndex] ? ' active' : '')}
                  style={getCellStyle(colIndex)}
                  onClick={() => toggleCell(rowIndex, colIndex)}
                  />
              ))}
              </div>
          ))}
          </div>
      </div>
      {/* Drum Volume Controls */}
      <div className="d-flex justify-content-around mt-3">
        {drumLabels.map((label, index) => (
          <Form.Group key={index} className="text-center">
            <Form.Label>{label}</Form.Label>
            <Form.Range
              min="0"
              max="100"
              value={drumVolumes[index]}
              onChange={(e) => handleDrumVolumeChange(index, parseInt(e.target.value))}
              style={{ width: '80px' }}
            />
          </Form.Group>
        ))}
      </div>
    </div>
  );
};

export default DrumMachine;