import React from 'react';
import { Tabs, Tab, Form } from 'react-bootstrap';
import DrumMachine from './DrumMachine';
import AdvancedSequencer from './AdvancedSequencer';

// Define types for sequencer states (must match App.tsx)
interface StepState {
  note: number;
  pLockValues: { [key: string]: number };
}

interface PartTabsProps {
  swing: number;
  swingMode: '16th' | '8th';
  activeStep: number;
  drumGrid: boolean[][];
  setDrumGrid: (newGrid: boolean[][]) => void;
  bassSteps: StepState[];
  setBassSteps: (newSteps: StepState[]) => void;
  padSteps: StepState[];
  setPadSteps: (newSteps: StepState[]) => void;
  leadSteps: StepState[];
  setLeadSteps: (newSteps: StepState[]) => void;
  drumVolumes: number[];
  setDrumVolumes: React.Dispatch<React.SetStateAction<number[]>>;
  bassVolume: number;
  setBassVolume: React.Dispatch<React.SetStateAction<number>>;
  padVolume: number;
  setPadVolume: React.Dispatch<React.SetStateAction<number>>;
  leadVolume: number;
  setLeadVolume: React.Dispatch<React.SetStateAction<number>>;
}

const PartTabs: React.FC<PartTabsProps> = ({ 
  swing, swingMode, activeStep,
  drumGrid, setDrumGrid,
  bassSteps, setBassSteps,
  padSteps, setPadSteps,
  leadSteps, setLeadSteps,
  drumVolumes, setDrumVolumes,
  bassVolume, setBassVolume,
  padVolume, setPadVolume,
  leadVolume, setLeadVolume
}) => {
  return (
    <Tabs defaultActiveKey="drums" id="part-tabs" className="mb-3" fill>
      <Tab eventKey="drums" title="Drums">
        <DrumMachine 
          swing={swing} 
          swingMode={swingMode} 
          activeStep={activeStep} 
          grid={drumGrid} 
          setGrid={setDrumGrid}
          drumVolumes={drumVolumes}
          setDrumVolumes={setDrumVolumes}
        />
      </Tab>
      <Tab eventKey="bass" title="Bass">
        <AdvancedSequencer 
          rows={8} cols={16} 
          swing={swing} 
          swingMode={swingMode} 
          activeStep={activeStep} 
          steps={bassSteps} 
          setSteps={setBassSteps}
          volume={bassVolume}
          setVolume={setBassVolume}
        />
        <Form.Group className="mt-3">
          <Form.Label>Sub Bass Level</Form.Label>
          <Form.Range min="0" max="100" defaultValue="0" />
        </Form.Group>
      </Tab>
      <Tab eventKey="pad" title="Pad">
        <AdvancedSequencer 
          rows={8} cols={16} 
          swing={swing} 
          swingMode={swingMode} 
          activeStep={activeStep} 
          steps={padSteps} 
          setSteps={setPadSteps}
          volume={padVolume}
          setVolume={setPadVolume}
        />
      </Tab>
      <Tab eventKey="lead" title="Lead">
        <AdvancedSequencer 
          rows={12} cols={16} 
          swing={swing} 
          swingMode={swingMode} 
          activeStep={activeStep} 
          steps={leadSteps} 
          setSteps={setLeadSteps}
          volume={leadVolume}
          setVolume={setLeadVolume}
        />
      </Tab>
    </Tabs>
  );
};

export default PartTabs;