import React, { useState, useRef, useEffect, useCallback } from 'react';
import './App.css';
import Header from './components/Header';
import PartTabs from './components/PartTabs';
import { PatternManager, SongArranger } from './components/Arrangement';
import { Container, Row, Col } from 'react-bootstrap';
import { AudioEngine } from './AudioEngine';

// Define types for sequencer states
interface StepState {
  note: number;
  pLockValues: { [key: string]: number };
}

interface PatternData {
  drumGrid: boolean[][];
  bassSteps: StepState[];
  padSteps: StepState[];
  leadSteps: StepState[];
}

type AllPatterns = { [patternId: string]: PatternData };

// Initial empty pattern data
const createEmptyPattern = (): PatternData => ({
  drumGrid: Array(4).fill(null).map(() => Array(16).fill(false)),
  bassSteps: Array(16).fill(null).map(() => ({ note: -1, pLockValues: {} })),
  padSteps: Array(16).fill(null).map(() => ({ note: -1, pLockValues: {} })),
  leadSteps: Array(16).fill(null).map(() => ({ note: -1, pLockValues: {} })),
});

function App() {
  // UI States
  const [swing, setSwing] = useState(50);
  const [swingMode, setSwingMode] = useState<'16th' | '8th'>('16th');
  const [isPlaying, setIsPlaying] = useState(false);
  const [activeStep, setActiveStep] = useState<number>(-1);

  // Pattern Management States
  const [allPatterns, setAllPatterns] = useState<AllPatterns>({
    'A1': createEmptyPattern(),
  });
  const [activePatternId, setActivePatternId] = useState<string>('A1');

  // Derived current pattern data
  const currentPattern = allPatterns[activePatternId];

  // Volume States
  const [drumVolumes, setDrumVolumes] = useState<number[]>([75, 75, 75, 75]); // BD, SD, CH, OH
  const [bassVolume, setBassVolume] = useState(75);
  const [padVolume, setPadVolume] = useState(75);
  const [leadVolume, setLeadVolume] = useState(75);

  // AudioEngine instance
  const audioEngineRef = useRef<AudioEngine | null>(null);
  const [isAudioReady, setIsAudioReady] = useState(false);

  // Initialize AudioEngine and set up step change callback
  useEffect(() => {
    if (!audioEngineRef.current) {
      const engine = new AudioEngine();
      engine.setOnStepChange(setActiveStep);
      audioEngineRef.current = engine;
    }
  }, []);

  // Update AudioEngine whenever relevant states change
  useEffect(() => {
    if (audioEngineRef.current && currentPattern) {
      audioEngineRef.current.updateSequencerState({
        drumGrid: currentPattern.drumGrid,
        bassSteps: currentPattern.bassSteps,
        padSteps: currentPattern.padSteps,
        leadSteps: currentPattern.leadSteps,
        swing,
        swingMode,
        drumVolumes,
        bassVolume,
        padVolume,
        leadVolume,
      });
    }
  }, [currentPattern, swing, swingMode, drumVolumes, bassVolume, padVolume, leadVolume]);

  const handlePlayToggle = async () => {
    if (!isAudioReady && audioEngineRef.current) {
      await audioEngineRef.current.start();
      setIsAudioReady(true);
    }

    const newIsPlaying = !isPlaying;
    setIsPlaying(newIsPlaying);
    audioEngineRef.current?.togglePlayback(newIsPlaying);
  };

  // --- Wrapper Setters for Pattern Data ---
  const updatePattern = useCallback((part: keyof PatternData, newData: any) => {
    setAllPatterns(prevPatterns => ({
      ...prevPatterns,
      [activePatternId]: {
        ...prevPatterns[activePatternId],
        [part]: newData,
      },
    }));
  }, [activePatternId]);

  const setDrumGrid = useCallback((newGrid: boolean[][]) => updatePattern('drumGrid', newGrid), [updatePattern]);
  const setBassSteps = useCallback((newSteps: StepState[]) => updatePattern('bassSteps', newSteps), [updatePattern]);
  const setPadSteps = useCallback((newSteps: StepState[]) => updatePattern('padSteps', newSteps), [updatePattern]);
  const setLeadSteps = useCallback((newSteps: StepState[]) => updatePattern('leadSteps', newSteps), [updatePattern]);

  return (
    <div className="App">
      <Header 
        swing={swing} 
        setSwing={setSwing} 
        swingMode={swingMode} 
        setSwingMode={setSwingMode}
        isPlaying={isPlaying}
        onPlayToggle={handlePlayToggle}
      />
      <Container fluid className="mt-3">
        <Row>
          <Col md={9}>
            <PartTabs 
              swing={swing} 
              swingMode={swingMode} 
              activeStep={activeStep}
              drumGrid={currentPattern.drumGrid}
              setDrumGrid={setDrumGrid}
              bassSteps={currentPattern.bassSteps}
              setBassSteps={setBassSteps}
              padSteps={currentPattern.padSteps}
              setPadSteps={setPadSteps}
              leadSteps={currentPattern.leadSteps}
              setLeadSteps={setLeadSteps}
              drumVolumes={drumVolumes}
              setDrumVolumes={setDrumVolumes}
              bassVolume={bassVolume}
              setBassVolume={setBassVolume}
              padVolume={padVolume}
              setPadVolume={setPadVolume}
              leadVolume={leadVolume}
              setLeadVolume={setLeadVolume}
            />
          </Col>
          <Col md={3}>
            <PatternManager 
              allPatterns={allPatterns}
              setAllPatterns={setAllPatterns}
              activePatternId={activePatternId}
              setActivePatternId={setActivePatternId}
              createEmptyPattern={createEmptyPattern}
            />
            <SongArranger />
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default App;