import React from 'react';
import { Card, ButtonGroup, Button, Dropdown } from 'react-bootstrap';

// Define types for pattern data (must match App.tsx)
interface StepState {
  note: number;
  pLockValues: { [key: string]: number };
}

interface PatternData {
  drumGrid: boolean[][];
  bassSteps: StepState[];
  padSteps: StepState[];
  leadSteps: StepState[];
}

type AllPatterns = { [patternId: string]: PatternData };

interface PatternManagerProps {
  allPatterns: AllPatterns;
  setAllPatterns: React.Dispatch<React.SetStateAction<AllPatterns>>;
  activePatternId: string;
  setActivePatternId: React.Dispatch<React.SetStateAction<string>>;
  createEmptyPattern: () => PatternData;
}

const PatternManager: React.FC<PatternManagerProps> = ({ 
  allPatterns, 
  setAllPatterns, 
  activePatternId, 
  setActivePatternId,
  createEmptyPattern
}) => {
  const availablePatternIds = Object.keys(allPatterns);

  const handleAddPattern = () => {
    let newId = 'A1';
    for (let i = 1; i < 26; i++) { // Try A1 to AZ
      const charCode = 'A'.charCodeAt(0) + Math.floor((i - 1) / 9);
      const num = (i - 1) % 9 + 1;
      newId = String.fromCharCode(charCode) + num;
      if (!allPatterns[newId]) break;
    }
    setAllPatterns(prev => ({ ...prev, [newId]: createEmptyPattern() }));
    setActivePatternId(newId);
  };

  const handleCopyPattern = () => {
    // For now, we'll just copy the current pattern to a temporary clipboard
    // In a real app, you might use a global clipboard state or local storage
    console.log('Copied pattern:', allPatterns[activePatternId]);
    alert(`Pattern ${activePatternId} copied! (Check console for data)`);
  };

  const handlePastePattern = () => {
    // This is a placeholder. In a real app, you'd paste from a clipboard.
    // For now, let's just create a new pattern from the current one as an example.
    const newId = `Pasted-${Math.random().toString(36).substring(7)}`;
    setAllPatterns(prev => ({ ...prev, [newId]: { ...allPatterns[activePatternId] } }));
    setActivePatternId(newId);
    alert(`Pattern pasted as ${newId}!`);
  };

  return (
    <Card bg="dark" text="white" className="mb-3">
      <Card.Body>
        <div className="d-flex justify-content-between align-items-center">
          <div>
            <span className="me-2">Pattern:</span>
            <Dropdown as={ButtonGroup}>
              <Button variant="primary">{activePatternId}</Button>
              <Dropdown.Toggle split variant="primary" id="dropdown-split-basic" />
              <Dropdown.Menu>
                {availablePatternIds.map(id => (
                  <Dropdown.Item key={id} onClick={() => setActivePatternId(id)}>{id}</Dropdown.Item>
                ))}
                <Dropdown.Divider />
                <Dropdown.Item onClick={handleAddPattern}>+ New Pattern</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </div>
          <div>
            <Button variant="outline-secondary" onClick={handleCopyPattern}>Copy</Button>
            <Button variant="outline-secondary" className="ms-2" onClick={handlePastePattern}>Paste</Button>
          </div>
        </div>
      </Card.Body>
    </Card>
  );
};

const SongArranger: React.FC = () => {
    return (
        <Card bg="dark" text="white">
            <Card.Header>Arrangement</Card.Header>
            <Card.Body style={{ minHeight: '150px', overflowX: 'auto' }}>
                <div style={{ display: 'flex', gap: '8px' }}>
                    <div className="text-center p-2 border rounded" style={{ minWidth: '80px'}}><div>Bar 1-4</div><div className='text-primary'>A1</div></div>
                    <div className="text-center p-2 border rounded" style={{ minWidth: '80px'}}><div>Bar 5-8</div><div className='text-primary'>A2</div></div>
                    <div className="text-center p-2 border rounded" style={{ minWidth: '80px'}}><div>Bar 9-12</div><div className='text-primary'>B1</div></div>
                    <div className="text-center p-2 border rounded" style={{ minWidth: '80px'}}><div>Bar 13-16</div><div className='text-primary'>A2</div></div>
                </div>
            </Card.Body>
        </Card>
    )
}

export { PatternManager, SongArranger };