import React, { useState } from 'react';
import { Form, Dropdown, DropdownButton } from 'react-bootstrap';
import './Sequencer.css';

interface AdvancedSequencerProps {
  rows: number;
  cols: number;
  swing: number;
  swingMode: '16th' | '8th';
  activeStep: number;
  steps: StepState[];
  setSteps: (newSteps: StepState[]) => void;
  volume: number;
  setVolume: React.Dispatch<React.SetStateAction<number>>;
}

interface StepState {
  note: number;
  pLockValues: { [key: string]: number };
}

const pLockParameters = ['Filter', 'Gate', 'Attack', 'Decay', 'Sustain', 'Release'];

const AdvancedSequencer: React.FC<AdvancedSequencerProps> = ({ rows, cols, swing, swingMode, activeStep, steps, setSteps, volume, setVolume }) => {
  const [selectedPLockStep, setSelectedPLockStep] = useState<number | null>(null);
  const [pLockTarget, setPLockTarget] = useState<string>(pLockParameters[0]);

  const setNote = (row: number, col: number) => {
    const newSteps = [...steps];
    const currentNote = newSteps[col].note;
    newSteps[col] = { ...newSteps[col], note: currentNote === row ? -1 : row };
    setSteps(newSteps);
  };

  const handlePLockValueChange = (col: number, value: number) => {
    const newSteps = [...steps];
    newSteps[col].pLockValues[pLockTarget] = value;
    setSteps(newSteps);
  };

  const getCellStyle = (colIndex: number) => {
    const swingRatio = (swing - 50) / 50;
    const baseWidth = 100 / cols;
    let width = baseWidth;
    let shouldSwing = swingMode === '16th' ? colIndex % 2 !== 0 : (colIndex % 4 === 2 || colIndex % 4 === 3);
    width += shouldSwing ? -baseWidth * swingRatio : baseWidth * swingRatio;
    return { flex: `0 0 ${width}%` };
  };

  const getCellClassName = (colIndex: number, rowIndex: number) => {
    const step = steps[colIndex];
    let className = 'sequencer-cell';
    if (step.note === rowIndex) className += ' active';
    if (colIndex === activeStep) className += ' playhead';
    return className;
  }

  return (
    <div>
      <div className="sequencer-grid">
        {/* Piano Roll */}
        {Array(rows).fill(0).map((_, rowIndex) => (
          <div key={rowIndex} className="sequencer-row">
            {Array(cols).fill(0).map((_, colIndex) => (
              <div
                key={colIndex}
                className={getCellClassName(colIndex, rowIndex)}
                style={getCellStyle(colIndex)}
                onClick={() => setNote(rowIndex, colIndex)}
              />
            ))}
          </div>
        ))}
        {/* P-Lock Row */}
        <div className="sequencer-row p-lock-row">
          {steps.map((step, colIndex) => (
            <div
              key={colIndex}
              className={`sequencer-cell p-lock-cell ${step.pLockValues[pLockTarget] !== undefined ? 'has-plock' : ''} ${colIndex === activeStep ? 'playhead' : ''}`}
              style={getCellStyle(colIndex)}
              onClick={() => setSelectedPLockStep(selectedPLockStep === colIndex ? null : colIndex)}
            >
              {step.pLockValues[pLockTarget] !== undefined && (
                <div className="p-lock-value-bar" style={{ height: `${step.pLockValues[pLockTarget]}%` }} />
              )}
            </div>
          ))}
        </div>
      </div>

      {/* P-Lock Controls */}
      <div className="p-lock-controls">
        <DropdownButton id="plock-target-dropdown" title={`P-Lock: ${pLockTarget}`} size="sm">
          {pLockParameters.map(param => (
            <Dropdown.Item key={param} onClick={() => setPLockTarget(param)}>{param}</Dropdown.Item>
          ))}
        </DropdownButton>

        {selectedPLockStep !== null && (
          <Form.Group className="ms-3 flex-grow-1">
            <Form.Label className="mb-0">{`Step ${selectedPLockStep + 1} ${pLockTarget}`}</Form.Label>
            <Form.Range
              min="0"
              max="100"
              value={steps[selectedPLockStep].pLockValues[pLockTarget] ?? 50}
              onChange={(e) => handlePLockValueChange(selectedPLockStep, parseInt(e.target.value))}
            />
          </Form.Group>
        )}
      </div>

      {/* Part Volume Control */}
      <Form.Group className="mt-3">
        <Form.Label>Part Volume</Form.Label>
        <Form.Range
          min="0"
          max="100"
          value={volume}
          onChange={(e) => setVolume(parseInt(e.target.value))}
        />
      </Form.Group>
    </div>
  );
};

export default AdvancedSequencer;
